let importAll = (requireContext: __WebpackModuleApi.RequireContext) =>
  requireContext.keys().forEach(requireContext);
try {
  importAll(require.context("../assets/icons", true, /\.svg$/));
} catch (error) {
  console.error();
}
import Vue from "vue";

export default Vue.extend({
  props: ["name"],
  name: "Icon",
});
